// axios
import axios from 'axios'
// import {ElMessage} from "element-plus";
import {ElLoading} from "element-plus";
// import store from '@/store/index' vuextokenuserinfostorestorage
// 
//https://api.test.esl-passport.cn/api/
//https://dev.api.eslpassport.com/api/
let baseUrl = process.env.VUE_APP_URL
// axios
axios.defaults.baseURL = baseUrl
// 
axios.defaults.timeout = 30000
// axios.defaults.withCredentials = true
// token
axios.interceptors.request.use(config => {
    const token = localStorage.getItem('token');
    const sourceUrl = window.location.href;

    if (token && token != '') {
        config.headers.token = token
        // token && (config.headers.Authorization = token)
    }
    config.headers.source = sourceUrl;
    config.headers.platform = 4;
    return config
}, error => {
    // elementuiui
    // console.log(error)
    return Promise.error(error)
})

// 
// axios.interceptors.response.use(response => {
//         return Promise.resolve(response.data)
//     }
//     )

// 
axios.interceptors.response.use(response => {
    // 200
    // 
    if(response && response.data){
        return Promise.resolve(response.data)
    }else{
        console.log('--------------------- response-------------------- ')
        // location.reload()
        return Promise.reject();
    }

}, error => {
    // 

    let errResponse = error.response

    // console.log(errResponse)

    if(errResponse){
        let status = errResponse.status

        if (status === 401) {
            localStorage.clear()
            const loadingInstance =  ElLoading.service({
                text:'The login information has expired, please login again'
            })
            setTimeout(function () {
                loadingInstance.close()
                window.location.href = '/login'
            },1000)

            return Promise.reject();
        } else {
            return Promise.reject(error.response.data);
        }

    }

    // 
    if (!errResponse) {
        console.log('--------------------- response-------------------- ')
        // location.reload()
        return Promise.reject();
    }
})

// 2
// @param url 
// @param data 
// @param file 
// @param auth token
// get
export function get(url, data) {
    return axios.get(url, {
        params: data
    })
}

// post
export function post(url, data) {
    return axios.post(url, data)
}

// put
export function put(url, data) {
    return axios.put(url, data)
}

// delete 
export function del(url, data) {
    return axios.delete(url, data)
}

// upload 
export function uploader(url, file) {
    let params = new FormData()
    params.append('file', file)
    return axios.post(url, params)
}
