import logoImgLogo from '@/assets/newHome/logo/Logo_Transparent.png';
import { useRouter } from 'vue-router';
import { ref } from 'vue';
export default {
  name: "mobileDrawerMenu",
  props: ['visible'],

  setup(props, context) {
    const router = useRouter();
    console.log(props); // const visible = props.visible

    const sMenuPath = ref(window.location.pathname);

    function turnHome() {
      context.emit('close');
      sMenuPath.value = '/';
      return router.push('/');
    }

    function turnJobs() {
      context.emit('close');
      sMenuPath.value = '/jobs';
      return router.push('/jobs');
    }

    function turnDeals() {
      context.emit('close');
      sMenuPath.value = '/deals';
      return router.push('/deals');
    }

    function turnEvents() {
      context.emit('close');
      sMenuPath.value = '/events';
      return router.push('/events');
    }

    function turnProfile() {
      context.emit('close');
      sMenuPath.value = '/overview';
      return router.push('/overview');
    }

    function turnLogin() {
      context.emit('close');
      sMenuPath.value = '/login';
      return router.push('/login');
    }

    function turnSignUp() {
      context.emit('close');
      sMenuPath.value = '/signup';
      return router.push('/signup');
    }

    function turnChinaWebsite() {
      context.emit('close');
      return window.open('https://www.edupassport.cn');
    }

    function turnGlobalWebsite() {
      context.emit('close');
      return window.open('https://www.edupassport.io');
    }

    function turnBlog() {
      context.emit('close');
      return window.open('https://blogs.edupassport.io/');
    }

    return {
      sMenuPath,
      turnHome,
      turnJobs,
      turnDeals,
      turnEvents,
      turnProfile,
      turnLogin,
      turnSignUp,
      turnChinaWebsite,
      turnGlobalWebsite,
      turnBlog
    };
  },

  data() {
    return {
      logoImgLogo,
      envName: process.env.VUE_APP_ENV_NAME,
      envBlog: process.env.VUE_APP_BLOG,
      menuDrawerStatus: false
    };
  },

  computed: {
    identity: {
      get() {
        return this.$store.state.identity;
      }

    },
    token: {
      get() {
        return localStorage.getItem('token');
      }

    }
  },
  methods: {
    closeMobileMenu() {
      this.$emit('close');
    }

  }
};